@import '../../styles/variables.scss';
@import '../../styles/mixins.scss';

.wrapper {
  @include text(1.1rem, 400, $darkText);
  background-color: $lightText;
  .container {
    overflow: auto;
    scroll-behavior: smooth;
    max-height: 75vh;
    position: relative;
    .fallback {
      text-align: center;
      padding: 0.5rem;
    }
  }
}
